import React from "react";
import { connect } from "react-redux";
import { StyledWriter, StyledWriterHeader, StyledWriterList, StyledWriterItem, StyledWriterItemLogo, StyledWriterItemDesc, StyledWriterMoreBtn } from "../style";

const Writer = ({ writerList }) => {
  return (
    <StyledWriter>
      <StyledWriterHeader>
        <span></span>
        <span><i className="iconfont">&#xe851;</i></span>
      </StyledWriterHeader>
      <StyledWriterList>
        {
          writerList.map((item) => (
            <StyledWriterItem key={item.get("id")}>
              <StyledWriterItemLogo src={item.get("imgUrl")} />
              <StyledWriterItemDesc>
                <div className="header">
                  <span className="name">{item.get("name")}</span>
                  <span className="fan"></span>
                </div>
                <div className="info">{item.get("words")}  {item.get("likes")}</div>
              </StyledWriterItemDesc>
            </StyledWriterItem>
          ))
        }
      </StyledWriterList>
      <StyledWriterMoreBtn>&nbsp;</StyledWriterMoreBtn>
    </StyledWriter>
  )
}

const mapState = (state) => ({
  writerList: state.getIn(['home', 'writerList'])
})

export default connect(mapState, null)(Writer);



