import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { StyledRouteLink, StyledReg, StyledLogo, StyledRegBox, StyledRegHeader, StyledRegInputWrapper, StyledRegBtn, StyledRegLimit, StyledRegTip, StyledRegSocial } from "./style";

const Reg = () => {
  return (
    <StyledReg>
      <Link to="/"><StyledLogo src="//cdn2.jianshu.io/assets/web/logo-58fd04f6f0de908401aa561cda6a0688.png" /></Link>
      <StyledRegBox>
        <StyledRegHeader>
          <StyledRouteLink to="login"><span className="login"></span></StyledRouteLink>
          <span></span>
          <StyledRouteLink to="reg"><span className="reg"></span></StyledRouteLink>
        </StyledRegHeader>
        <StyledRegInputWrapper>
          <input type="text" className="nickname" placeholder=""/>
          <i className="iconfont">&#xe66c;</i>
        </StyledRegInputWrapper>
        <StyledRegInputWrapper>
          <input type="text" className="phone" placeholder=""/>
          <i className="iconfont">&#xe61d;</i>
        </StyledRegInputWrapper>
        <StyledRegInputWrapper>
          <input type="text" className="psw" placeholder=""/>
          <i className="iconfont">&#xe6a8;</i>
        </StyledRegInputWrapper>
        <StyledRegBtn></StyledRegBtn>
        <StyledRegLimit>  <br /><a href=""></a>  <a href=""></a> </StyledRegLimit>
        <StyledRegTip><span className="line"></span><span className="tip"></span><span className="line"></span></StyledRegTip>
        <StyledRegSocial>
          <i className="iconfont weibo">&#xe737;</i>
          <i className="iconfont weixin">&#xe637;</i>
          <i className="iconfont qq">&#xe607;</i>
          <i className="iconfont douban">&#xe6b5;</i>
        </StyledRegSocial>
      </StyledRegBox>
    </StyledReg>
  )
}

export default connect(null, null)(Reg);



